@import '../../assets/css/variable.less';

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 72px;

  .left {
    display: flex;
    align-items: center;
    .logo {
      margin-left : 48px;
      display: flex;
      align-items: center;
      .name {
        margin-left: 12px;
        color: @whiteFontColor;
        font-size: @20FontSize;
      }
    }
  }
  .right {
    display: flex;
    margin-right: 10px;
  }

}
